.s2-form * {
	font-family: "Poppins", sans-serif;
}
.s2-form {
	padding-top: 20px;
	text-align: center;
	background-color: white;
}
.s2-form__container {
	max-width: 1062px;
	margin: 0 auto;
}
.s2-form__step {
	display: none;
}
.s2-form__title {
	font-weight: 600;
	font-size: 32px;
	line-height: 106%;
	margin-bottom: 22px;
}
.s2-form__title.big {
	font-size: 48px;
	line-height: 71%;
	margin-bottom: 14px;
}

.s2-form__subtitle,
.s2-form__subtitle p {
	font-weight: 600;
	font-size: 24px;
	line-height: 142%;
	margin-bottom: 33px;
	text-align: center;
}
.s2-form__title-custom {
	background-color: #f5f5f5;
	padding: 20px 0 20px 0 !important;
	border-block-end: none !important;
}
.s2-form__title-custom option,
.s2-form__form .gf_placeholder {
	text-align: left;
}
.s2-form__form .gfield option,
.s2-form__form .gfield textarea,
.s2-form__form .gfield_description {
	text-align: left;
}
.s2-form__form .gfield {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.s2-form__form .ginput_container select,
.s2-form__form .ginput_container input,
.s2-form__form .ginput_container_text input {
	border-color: #dddddd !important;
	padding: 0 18px;
	min-height: 50px !important;
	height: auto !important;
	box-shadow: none;
	text-align: left;
	line-height: 50px;
}
.s2-form__title-custom,
.s2-form__title-custom h3 {
	font-size: 36px;
	font-weight: 600;
	line-height: 34px;
	margin: 0;
}
.s2-form__thanks {
	padding: 80px 0 !important;
}
.s2-form__thanks h3 {
	margin-bottom: 14px;
	font-size: 48px;
	line-height: 34px;
	font-weight: 600;
}
.s2-form__thanks p {
	font-weight: 600;
	line-height: 34px;
	font-size: 24px;
	margin: 0;
}
.s2-form__title * {
	font-size: inherit;
	line-height: inherit;
	font-weight: inherit;
	margin: 0;
}
.s2-form__progress > span {
	display: block;
	width: 40px;
	height: 40px;
	border: 1px solid #c6c6c6;
	border-radius: 50%;
	background: #fff;
	position: relative;
	z-index: 2;
}
.s2-form__progress::before,
.s2-form__progress::after {
	content: "";
	position: absolute;
	top: 50%;
	left: 20px;
	right: 20px;
	height: 1px;
}
.s2-form__progress::before {
	z-index: 1;
	background: #c6c6c6;
}
.s2-form__progress::after {
	z-index: 1;
	background: #0043ce;
	right: unset;
	width: Min(var(--progress, 0%), calc(100% - 20px));
}
.s2-form__progress {
	max-width: 566px;
	width: 100%;
	margin: 0 auto 80px;
	display: flex;
	position: relative;
	justify-content: space-between;
}

.s2-form__progress > span.done {
	background-image: url('data:image/svg+xml,%3Csvg width="36" height="37" viewBox="0 0 36 37" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M15.8699 24.0027C15.5699 24.0027 15.2849 23.8827 15.0749 23.6727L10.8299 19.4277C10.3949 18.9927 10.3949 18.2727 10.8299 17.8377C11.2649 17.4027 11.9849 17.4027 12.4199 17.8377L15.8699 21.2877L23.5799 13.5777C24.0149 13.1427 24.7349 13.1427 25.1699 13.5777C25.6049 14.0127 25.6049 14.7327 25.1699 15.1677L16.6649 23.6727C16.4549 23.8827 16.1699 24.0027 15.8699 24.0027Z" fill="white" /%3E%3C/svg%3E');
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	background-color: #0043ce;
	border-color: #0043ce;
}

.s2-form__progress > span.process {
	background-image: url('data:image/svg+xml,%3Csvg width="36" height="37" viewBox="0 0 36 37" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Ccircle cx="18" cy="18.6328" r="4.5" fill="%230043CE" /%3E%3C/svg%3E');
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	border-color: #0043ce;
}
.s2-form__progress > span {
	background-color: #fff;
}

.s2-form__video {
	max-width: 1060px;
	margin: 0 auto;
}
.s2-form__btn,
.s2-form__form .gform-theme.gform-theme--framework.gform_wrapper .gform_footer > [type="submit"].gform_button {
	display: inline-block;
	text-transform: uppercase;
	text-align: center;
	color: #fff;
	border: 0;
	outline: 0;
	cursor: pointer;
	transition:
		color 0.3s ease-in-out,
		background-color 0.3s ease-in-out;
	margin-top: 60px !important;
	font-weight: 700 !important;
	font-size: 20px !important;
	line-height: 120% !important;
	padding: 22px 50px 18px !important;
	background-color: #00b43d !important;
	border-radius: 16px !important;
}
.s2-form__btn:hover,
.s2-form__form .gform-theme.gform-theme--framework.gform_wrapper .gform_footer > [type="submit"].gform_button:hover {
	background: #13a243;
}
.s2-form__form .gform_wrapper .gform_heading {
	display: none;
}
.s2-form__form .gform_wrapper label {
	font-size: 1rem !important;
	display: block;
	text-align: left;
}

.s2-form__form  .gform_wrapper .gfield--input-type-address {
	display: none !important;
}

.s2-form__form {
	margin-top: 60px;
}

.s2-form__form .gfield option,
.s2-form__form .gfield textarea,
.s2-form__form .gfield_description {
	border-color: #dddddd;
}

.s2-form__form .gform-theme--foundation .gform_footer,
.s2-form__form .gform-theme--foundation .gform_page_footer {
	justify-content: center;
	--gf-form-footer-margin-y-start: 0;
}
.s2-form__form .gform_fields {
	--gf-form-gap-y: 24px;
}

.s2-form__title-custom:not(:first-child) {
	margin-top: 50px;
}



.s2-form__form .gfield_validation_message,
.s2-form__form .error-message-container {
	background-color: #feebe7;
	border: 1px solid #c02b0a;
	border-radius: .25rem;
	color: #c02b0a;
	font-size: .75rem;
	margin-top: .5rem;
	padding: .5rem;
}

@media (min-width: 1024px) {
	.s2-form__form .gfield:not(#field_12_56):not(#field_12_55):not(#field_12_68):not(#field_12_4){
		margin-bottom: 18px;
		position: relative;
	}
	.s2-form__form .gfield_validation_message,
	.s2-form__form .error-message-container {
		position: absolute;
		left: 0;
		right: 0;
		bottom: -3px;
		transform: translateY(100%);
		text-align: left;
	}
}


@media (max-width: 1023px) {
	.s2-form {
		max-width: 328px;
		margin: 0 auto;
		padding-bottom: 32px;
		padding-top: 30px;
	}

	.s2-form__subtitle,
	.s2-form__subtitle p {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 30px;
	}

	.s2-form__progress {
		margin-bottom: 52px;
	}

	.s2-form__title.big {
		font-size: 32px;
		line-height: 34px;
	}

	.s2-form__btn,
	.s2-form__form .gform-theme.gform-theme--framework.gform_wrapper .gform_footer > [type="submit"].gform_button {
		margin-top: 32px !important;
	}

	.s2-form__title-custom,
	.s2-form__title-custom h3 {
		font-size: 32px;
	}

	.s2-form__title-custom {
		padding: 20px !important;
		text-align: center;
	}

	.s2-form__thanks h3 {
		font-size: 32px;
	}

	.s2-form__thanks p {
		line-height: 24px;
		font-size: 20px;
	}

	.s2-form__thanks {
		padding: 50px 20px !important;
	}

	.s2-form__form .gform-theme--framework {
		text-align: left;
	}

}
